import React from 'react';
import Page from 'components/Layout/Page';
import { Container, Grid } from '@material-ui/core';
import { useStaticQuery, graphql } from 'gatsby';
import { Tool } from 'src/models';
import ToolCard from 'components/Cards/ToolCard';

interface ToolsResult {
  allToolsJson: {
    nodes: Tool[];
  };
}
const Tools = () => {
  const {
    allToolsJson: { nodes: tools },
  } = useStaticQuery<ToolsResult>(graphql`
    query AllToolsQuery {
      allToolsJson {
        nodes {
          name
          url
          icon {
            childImageSharp {
              fixed(width: 600, height: 600, quality: 50, cropFocus: CENTER) {
                ...GatsbyImageSharpFixed_noBase64
              }
            }
          }
          description
          dri
          admin
        }
      }
    }
  `);

  return (
    <Page
      pageName="Tools"
      showTitle={true}
      description={`루비콘에서 사용하고 있는 솔루션 및 툴입니다. 만약 새로운 계정 발급을 원하신다면 루비콘 슬랙에서 이야기해주세요!\nDRI는 해당 툴의 계정 관리 권한을 의미하고, 어드민은 해당 툴의 어드민 계정이 어떤 계정인지를 의미합니다.`}
    >
      <Container>
        <Grid container spacing={3}>
          {tools.map((tool, index) => (
            <Grid item xs={12} md={3} key={index}>
              <ToolCard tool={tool} />
            </Grid>
          ))}
        </Grid>
      </Container>
    </Page>
  );
};

export default Tools;
